import React, { useState } from 'react'
import { TfiMenu, TfiClose } from 'react-icons/tfi';
import logo from '../image/phc pavers logo transparent.png'

const Header = () => {

    const [toggle, setToggle] = useState(false);
    
    return (
        <div className='bg-[#f0f0f0] p-4 rounded-[100px] mt-2' id='home'>
            <div className="max-w-[1240px] py-[15px] mx-auto flex justify-between items-center">
                <a href="#home"> <img src={logo} alt="Phc Paver Block" className='bg-transparent h-[200px] w-[200px]' /></a>

                <TfiMenu className={`hidden sm:block text-black text-[20px] w-[50px] h-[60px] ${toggle?'sm:hidden':'' }`} onClick={() => { setToggle(!toggle) }} />
                <TfiClose className={`hidden  w-[50px] h-[60px] ${toggle?'sm:block':'sm:hidden' }`} onClick={() => { setToggle(!toggle) }} />

                <ul className='flex sm:hidden text-black gap-20 font-medium' >

                    <li className='font-bold font-[cursive] text-[25px] hover:scale-90 duration-500'>
                        <a href='#home' >Home</a>
                    </li>
                    <li className='font-bold font-[cursive] text-[25px] hover:scale-90 duration-500'>
                        <a href='#products' >Products</a>
                    </li>
                    <li className='font-bold font-[cursive] text-[25px] hover:scale-90 duration-500'>
                        <a href='#galary'>Galary</a>
                    </li>
                    <li className='font-bold font-[cursive] text-[25px] hover:scale-90 duration-500'>
                        <a href='#about'>About</a>
                    </li>
                    <li className='font-bold font-[cursive] text-[25px] hover:scale-90 duration-500'>
                        <a href='#contact'>Contact </a>
                    </li>

                </ul>

                {/* responsive menu */}
                <ul className={`duration-400 text-[20px] w-full h-screen sm:show absolute text-white gap-20 font-medium bg-black
                    ${toggle ? 'left-[0]' : 'left-[-100%]'} rounded-[100px] text-center top-[300px]`} >

                    <li className='p-[10px] font-bold font-[cursive] text-[25px] hover:scale-90 duration-500'>
                        <a href='#home' onClick={() => {setToggle(!toggle) }}>Home</a>
                    </li>
                    <li className='p-[10px] font-bold font-[cursive] text-[25px] hover:scale-90 duration-500'>
                        <a href='#products' onClick={() => { setToggle(!toggle) }}>Products</a>
                    </li>
                    <li className='p-[10px] font-bold font-[cursive] text-[25px] hover:scale-90 duration-500'>
                        <a href='#galary' onClick={() => { setToggle(!toggle) }}>Galary</a>
                    </li>
                    <li className='p-[10px] font-bold font-[cursive] text-[25px] hover:scale-90 duration-500'>
                        <a href='#about' onClick={() => { setToggle(!toggle) }}>About</a>
                    </li>
                    <li className='p-[10px] font-bold font-[cursive] text-[25px] hover:scale-90 duration-500'>
                        <a href='#contact' onClick={() => { setToggle(!toggle) }}>Contact </a>
                    </li>

                </ul>

            </div>
        </div>

    )
}

export default Header;